import AddPriceList from 'pages/costModels/components/addPriceList';
import React from 'react';
import { assign, interpret, Machine } from 'xstate';
import { CostModelContext } from './context';
import PriceListTable from './priceListTable';
const priceListMachine = ({ items, sideEffectSubmit, sideEffectEnabler, }) => Machine({
    id: 'price-list-step-machine',
    context: {
        items,
    },
    initial: 'table',
    states: {
        table: {
            entry: ['enableNext'],
            on: {
                ADD_RATE: 'form',
                DELETE_RATE: [
                    {
                        target: 'form',
                        actions: ['deleteRate'],
                        cond: 'isEmpty',
                    },
                    {
                        target: 'table',
                        actions: ['deleteRate'],
                    },
                ],
            },
        },
        form: {
            entry: ['disableNext'],
            on: {
                SUBMIT: {
                    target: 'table',
                    actions: ['addNewRate'],
                },
                CANCEL: 'table',
            },
        },
    },
}, {
    actions: {
        enableNext: (ctx, _evt) => {
            if (sideEffectSubmit) {
                sideEffectSubmit(ctx.items);
            }
            if (sideEffectEnabler) {
                sideEffectEnabler(true);
            }
        },
        disableNext: (_ctx, _evt) => {
            if (sideEffectEnabler) {
                sideEffectEnabler(false);
            }
        },
        deleteRate: assign({
            items: (ctx, evt) => {
                if (evt.type !== 'DELETE_RATE') {
                    return ctx.items;
                }
                const ixToSlice = ctx.items.findIndex(tier => tier.metric === evt.value.metric &&
                    tier.measurement === evt.value.measurement);
                if (ixToSlice === -1) {
                    return ctx.items;
                }
                return [
                    ...ctx.items.slice(0, ixToSlice),
                    ...ctx.items.slice(ixToSlice + 1),
                ];
            },
        }),
        addNewRate: assign({
            items: (ctx, evt) => {
                if (evt.type !== 'SUBMIT') {
                    return ctx.items;
                }
                return [...ctx.items, evt.value];
            },
        }),
    },
    guards: {
        isEmpty: (ctx, evt) => {
            if (evt.type !== 'DELETE_RATE') {
                return false;
            }
            const { items: tiers } = ctx;
            const { measurement, metric } = evt.value;
            return Boolean(tiers.length === 1 &&
                tiers[0].metric === metric &&
                tiers[0].measurement === measurement);
        },
    },
});
export class PirceListBase extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            current: null,
        };
        this.service = null;
        const { items, setNextButton, submit } = props;
        const stateMachine = priceListMachine({
            items,
            sideEffectSubmit: submit,
            sideEffectEnabler: setNextButton,
        });
        this.state = {
            current: stateMachine.initialState,
        };
        this.service = interpret(stateMachine).onTransition(current => this.setState({ current }));
    }
    componentDidMount() {
        this.service.start();
    }
    componentWillUnmount() {
        this.service.stop();
    }
    render() {
        const { metricsHash } = this.props;
        const { current } = this.state;
        const { send } = this.service;
        const stateName = current.toStrings()[0];
        const { items } = current.context;
        switch (stateName) {
            case 'table':
                return (React.createElement(PriceListTable, { items: items, deleteRateAction: data => send({ type: 'DELETE_RATE', value: data }), addRateAction: () => send('ADD_RATE') }));
            case 'form':
                return (React.createElement(AddPriceList, { metricsHash: metricsHash, items: items, submitRate: data => send({ type: 'SUBMIT', value: data }), cancel: () => send('CANCEL') }));
            default:
                return null;
        }
    }
}
const PriceList = () => {
    return (React.createElement(CostModelContext.Consumer, null, ({ metricsHash, goToAddPL, submitTiers, tiers }) => {
        return (React.createElement(PirceListBase, { items: tiers, metricsHash: metricsHash, setNextButton: (value) => goToAddPL(value), submit: newTiers => submitTiers(newTiers) }));
    }));
};
export default PriceList;
