import { SortByDirection } from '@patternfly/react-table';
export const costModelsTableMap = {
    updated_timestamp: 4,
    name: 0,
    source_type: 2,
};
export const reverseMap = (map) => {
    return Object.keys(map).reduce((acc, cur) => {
        return Object.assign(Object.assign({}, acc), { [map[cur]]: cur });
    }, {});
};
export const getSortByData = (sortBy, mapper) => {
    if (sortBy === null) {
        return {};
    }
    const sortName = sortBy[0] === '-' ? sortBy.slice(1) : sortBy;
    const index = mapper[sortName];
    const direction = sortBy[0] === '-' ? SortByDirection.desc : SortByDirection.asc;
    return { index, direction };
};
